// Converted Variables
// Custom Media Query Variables
/*

FONT WEIGHT
Docs: http://tachyons.io/docs/typography/font-weight/

Base
  fw = font-weight

Modifiers:
  1 = literal value 100
  2 = literal value 200
  3 = literal value 300
  4 = literal value 400
  5 = literal value 500
  6 = literal value 600
  7 = literal value 700
  8 = literal value 800
  9 = literal value 900

Media Query Extensions:
  -ns = not-small
  -m  = medium
  -l  = large

*/

.normal#{$sfx} {
  font-weight: normal;
}
.b#{$sfx} {
  font-weight: bold;
}
.fw1#{$sfx} {
  font-weight: 100;
}
.fw2#{$sfx} {
  font-weight: 200;
}
.fw3#{$sfx} {
  font-weight: 300;
}
.fw4#{$sfx} {
  font-weight: 400;
}
.fw5#{$sfx} {
  font-weight: 500;
}
.fw6#{$sfx} {
  font-weight: 600;
}
.fw7#{$sfx} {
  font-weight: 700;
}
.fw8#{$sfx} {
  font-weight: 800;
}
.fw9#{$sfx} {
  font-weight: 900;
}