// Converted Variables
// Custom Media Query Variables
/*

FLEXBOX

Media Query Extensions:
-ns = not-small
-m  = medium
-l  = large

*/

.flex#{$sfx} {
  display: flex;
}
.inline-flex#{$sfx} {
  display: inline-flex;
}
/* 1. Fix for Chrome 44 bug.
* https://code.google.com/p/chromium/issues/detail?id=506893 */
.flex-auto#{$sfx} {
  flex: 1 1 auto;
  min-width: 0;
  /* 1 */
  min-height: 0;
  /* 1 */
}
