@import
// WORDPRESS?
// 'wordpress-header';

// SETTINGS: used with preprocessors and contain font, colors definitions, etc
// 'colors',
'all-settings', // fonts, grid, media queries

// TOOLS: globally used mixins and functions
'mixins',
// 'themes',

// GENERIC: reset and/or normalize styles
'scss/normalize',

// ELEMENTS: styling for bare HTML elements
'html',
// 'forms',

// OBJECTS: class-based selectors which define undecorated design patterns (eg. media-object)
// 'grid', // ie10+ grid using flexbox, margins and calc and is nestable
// You should start putting new stuff here

// COMPONENTS: specific UI components
'header-footer',
// ...and here

// TRUMPS: utilities and helper classes with ability to override everything above
'helper',

'scss/variables',
'tachyons-mixins';
// 'tachyons', // scss folder contains all the tachyons partials

$sfx: '';
@import 'tachyons';

@each $name, $val in cfg(breakpoint) {
    $sfx: '-'+$name !global;
    @media #{$val} {
        @import 'tachyons-mediaqueried';
    }
}

@import 'z-index';
